
import type { GuaInfo } from "@/types/common";
type GenerateGua = (arg: GuaInfo) => void;
import { useStore } from "vuex";
import { computed, defineComponent, ref } from "vue";

import Nzh from "nzh";
const { cn: nzhcn } = Nzh;

export default defineComponent({
  name: "GuaView",
  setup() {
    const store = useStore();
    const content = ref<string[]>([]);
    const calContent = computed(() =>
      content.value.length === 1
        ? content.value
        : content.value.map((text, idx) => {
            if (idx === 0) {
              return `主 => ${text}`;
            } else if (idx === 1) {
              return `辅 => ${text}`;
            }
          })
    );
    const clearContent = () => {
      content.value = [];
    };
    const generateGua: GenerateGua = ({
      totalTrans,
      oYao,
      oGuaId,
      tGuaId,
      isUseO,
      isUseT,
      yaoIdList,
    }) => {
      if (
        oGuaId.length !== 6 ||
        tGuaId.length !== 6 ||
        (yaoIdList.length !== 1 && yaoIdList.length !== 2)
      ) {
        window.alert("error: 找旦旦提个bug吧~");
        return;
      }
      console.log({
        totalTrans,
        oYao,
        oGuaId,
        tGuaId,
        isUseO,
        isUseT,
        yaoIdList,
      });
      content.value = yaoIdList.map((item) => {
        const curGua = store.getters.getGuaById(item[0]);
        return `
          卦:${curGua.name} =>
          ${item[1] === "0" ? "卦辞" : nzhcn.encodeS(item[1]) + "爻"}:
          ${curGua.info[item[1]]}`;
      });
    };
    return {
      calContent,
      generateGua,
      clearContent,
    };
  },
});
